.App {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
    -ms-flex: 1 1;
        flex: 1 1;
}

.App__header {
    padding: 8px 8px;
}

.App__content {
    -ms-flex: 1 1;
        flex: 1 1;
}

.App__table {
    height: 100%;
}

.App__file-btn {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.App__file-btn + label {
    font-size: 1em;
    font-weight: 700;
    color: #111;
    background: -webkit-gradient(linear, left top, left bottom, from(white), to(lightgrey));
    background: -webkit-linear-gradient(white, lightgrey);
    background: -o-linear-gradient(white, lightgrey);
    background: linear-gradient(white, lightgrey);
    border: 1px solid darkgrey;
    display: inline-block;
    padding: 2px 8px;
    cursor: pointer;
}

.App__file-btn + label:hover {
    background: lightgrey;
}


